.contactsWrapper {
  width: 100%;
  height: 100%;
  border: 1px solid #dde1ee;

  @media screen and (min-width: 768px) {
    width: 50%;
  }
}
.contentWrapper {
  width: 100%;
  height: calc(100% - 98px);
}
.contactDetailsWrapper {
  position: absolute;
  top: 0;
  left: 0;
  @media screen and (min-width: 768px) {
    position: static;
    width: 50%;
  }
}
